.nav {
    width:100%;
    color:$white;
    background-image:linear-gradient(145deg, rgba($color-primary-mid, 1), rgba($color-primary-light, 1));
    display:flex;
    justify-content: space-between;
    align-items:center;
    @media (max-width: $phone){
        flex-direction:column;
    }
    &__links {
        width:15rem;
        display:flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: $phone){
            width:25rem;
        }
        

        & a {text-decoration:none;}
        & img {
            width:4rem;
            transition: all .2s;
            &:hover {
                transform:scale(1.2);
            }
            @media (max-width: $phone){
                width:6rem;
            }
        }
    }

        

 

